import Vue from 'vue'
import App from './App.vue'
import Vuetify from 'vuetify'
import i18n from './i18n'
import 'vuetify/dist/vuetify.css'

Vue.use(Vuetify, {
  theme: {
    primary: '#ffd600',
    secondary: '#424242',
    accent: '#82B1FF',
    error: '#FF5252',
    info: '#2196F3',
    success: '#4CAF50',
    warning: '#FFC107'
  }
})

new Vue({
  el: '#app',
  i18n,
  render: h => h(App)
})



