import Vue from 'vue'
import VueI18n from 'vue-i18n'

const data = require('./i18n.json')

Vue.use(VueI18n)
let locale = (window.navigator.languages && window.navigator.languages[0]) || window.navigator.language || window.navigator.userLanguage || window.navigator.browserLanguage
if (locale === undefined || locale === '') locale = 'ja'

export default new VueI18n({
  locale: locale,
  fallbackLocale: 'en',
  messages: data
})



